import React, { useRef, useContext } from "react";

const ComponentRefContext = React.createContext();

export function useComponentRef() {
  return useContext(ComponentRefContext);
}

export function ComponentRefProvider({ children }) {
  const componentRef = useRef();

  return (
    <ComponentRefContext.Provider value={componentRef}>
      {children}
    </ComponentRefContext.Provider>
  );
}
