import * as d3 from "d3";
import * as d3Sankey from "d3-sankey";
import { useCallback, useEffect, useRef, useState } from "react";
import "../D3//D3sankey.css";
import React from "react";
import { useSelector } from "react-redux";
import ColorLegend from "../ColorLegend/ColorLegend";
import { flagEndpoint } from "../../constant";
import { PuffLoader } from "react-spinners";
import { useComponentRef } from "../../Context/svgContext";

const D3sankey = () => {
  const object = useSelector((state) => state.apiData.object);

  const apiDataValue = useSelector((state) => state.apiData);

  const count = useSelector((state) => state.apiData.count);

  const componentRef = useComponentRef();

  const [scale, setScale] = useState(1);

  const incremtRef = useRef("");
  const decrementRef = useRef("");

  const findIndex = useCallback(
    (source) => {
      let res = object.links.find((item) => item.source === source);
      if (res === undefined) {
        return "grey";
      } else {
        let index = object.links.indexOf(res);
        return object.links[index].color;
      }
    },
    [object]
  );

  useEffect(() => {
    async function SankeyChart(
      {
        nodes, // an iterable of node objects (typically [{id}, …]); implied by links if missing
        links, // an iterable of link objects (typically [{source, target}, …])
      },
      {
        format = ",", // a function or format specifier for values in titles
        align = "justify", // convenience shorthand for nodeAlign
        nodeId = (d) => d.id, // given d in nodes, returns a unique identifier (string)
        nodeGroup, // given d in nodes, returns an (ordinal) value for color
        nodeImg = ({ image }) => image,
        nodeGroups, // an array of ordinal values representing the node groups
        nodeLabel, // given d in (computed) nodes, text to label the associated rect
        nodeTitle = (d) => `${d.id}\n${format(d.value)}`, // given d in (computed) nodes, hover text
        nodeAlign = align, // Sankey node alignment strategy: left, right, justify, center
        nodeWidth = 40, // width of node rects
        nodePadding = 15, // vertical separation between adjacent nodes
        linkSource = ({ source }) => source, // given d in links, returns a node identifier string
        linkTarget = ({ target }) => target, // given d in links, returns a node identifier string
        linkValue = ({ value }) => value, // given d in links, returns the quantitative value
        linkTitle = (d) =>
          `${d.source.id} → ${d.target.id}\n${format(d.value)}`, // given d in (computed) links
        colors = d3.schemeTableau10, // array of colors
        width = 1000, // outer width, in pixels
        height = 600, // outer height, in pixels
        marginTop = 5, // top margin, in pixels
        marginRight = 1, // right margin, in pixels
        marginBottom = 5, // bottom margin, in pixels
        marginLeft = 1, // left margin, in pixels
      } = {}
    ) {
      var DOM;

      document.getElementById("sankey").innerHTML = "";

      let colorData = [];

      // console.log(object[count].links.length);
      await object.links.forEach((element) => {
        colorData.push(element.color);
      });

      colors = colorData;

      console.log("links",object.links)

      // Convert nodeAlign from a name to a function (since d3-sankey is not part of core d3).
      if (typeof nodeAlign !== "function")
        nodeAlign =
          {
            left: d3Sankey.sankeyLeft,
            right: d3Sankey.sankeyRight,
            center: d3Sankey.sankeyCenter,
          }[nodeAlign] ?? d3Sankey.sankeyJustify;
      // Compute values.
      const LS = d3.map(object.links, linkSource).map(intern);
      const LT = d3.map(object.links, linkTarget).map(intern);
      const LV = d3.map(object.links, linkValue);
      const Img = d3.map(object.nodes, nodeImg);

      if (nodes === undefined)
      nodes = Array.from(d3.union(LS, LT), (id) => ({ id }));
      const N = d3.map(nodes, nodeId).map(intern);
      const G = nodeGroup == null ? null : d3.map(nodes, nodeGroup).map(intern);
      /// Replace the input nodes and links with mutable objects for the simulation.
      nodes = d3.map(object.nodes, (el, i) => ({
        id: N[i],
        value: LV[i],
        image: Img[i],
      }));

      links = d3.map(object.links, (el, i) => ({
        source: LS[i],
        target: LT[i],
        value: LV[i],
      }));

      // if (!G && colors) linkColor = colors;
      // Compute default domains.
      if (G && nodeGroups === undefined) nodeGroups = G;

      // Construct the scales.
      const color =
        nodeGroup == null ? null : d3.scaleOrdinal(nodeGroups, colors);

      // Compute the Sankey layout.
      d3Sankey
        .sankey()
        .nodeId(({ index: i }) => N[i])
        .nodeAlign(nodeAlign)
        .nodeWidth(nodeWidth)
        .nodePadding(nodePadding)
        .extent([
          [marginLeft, marginTop],
          [width - marginRight, height - marginBottom],
        ])({ nodes, links });

      const sankey = d3Sankey
        .sankey()
        .nodeId((d) => d.name)
        .nodeAlign(10)
        .nodeWidth(15)
        .nodePadding(10)
        .extent([
          [1, 5],
          [900 - 1, 600 - 5],
        ]);

      // Compute titles and labels using layout nodes, so as to access aggregate values.
      if (typeof format !== "function") format = d3.format(format);
      const Tl =
        nodeLabel === undefined
          ? N
          : nodeLabel == null
          ? null
          : d3.map(nodes, nodeLabel);
      const Tt = nodeTitle == null ? null : d3.map(object.nodes, nodeTitle);
      const Lt = linkTitle == null ? null : d3.map(object.links, linkTitle);

      // A unique identifier for clip paths (to avoid conflicts).
      // const uid = `O-${Math.random().toString(16).slice(2)}`;

      const svg = d3
        .select("#sankey")
        .append("svg")
        .attr("width", width)
        .attr("height", height)
        .attr("viewBox", [0, 0, width, height])
        .attr("style", "max-width: 100%; height: auto; height: intrinsic;");
      // Container for draggable nodes
      const node = svg
        .selectAll("g")
        .data(nodes)
        .join("g")
        .attr("transform", (d) => `translate(${d.x0}, ${d.y0})`);

      // Relative to container
      node
        .append("rect")
        .attr("height", (d) => d.y1 - d.y0)
        .attr("width", (d) => d.x1 - d.x0)
        .attr("fill", color)
        .attr("stroke", "#000")
        .append("title")
        .text((d) => `${d.source}\n${format(d.value)}`);

      node
        .append("image")
        .attr("height", (d) => d.y1 - d.y0)
        .attr("width", (d) => d.x1 - d.x0)
        .attr("href", (d) => d.image);

      // Relative to container/ node rect
      node
        .append("text")
        .attr("font-family", "sans-serif")
        .attr("font-size", 10)
        .data(nodes)
        .attr("x", (d) => (d.x0 < width / 2 ? 6 + (d.x1 - d.x0) : -6)) // +/- 6 pixels relative to container
        .attr("y", (d) => (d.y1 - d.y0) / 2) // middle of node
        .attr("dy", "0.35em")
        .attr("text-anchor", (d) => (d.x0 < width / 2 ? "start" : "end"))
        .text(({ index: i }) => Tl[i]);

      if (G) node.attr("fill", ({ index: i }) => color(G[i]));
      if (Tt) node.append("title").text(({ index: i }) => Tt[i]);

      // d3V6 events: https://observablehq.com/@d3/d3v6-migration-guide#events
      node
        .attr("cursor", "move")
        .call(d3.drag().on("start", dragStart).on("drag", dragMove));

      var edgeColor;

      function intern(value) {
        return value !== null && typeof value === "object"
          ? value.valueOf()
          : value;
      }

      const link = svg
        .append("g")
        .attr("fill", "none")
        .attr("stroke-opacity", 0.5)
        .selectAll("g")
        .data(links)
        .join("g")
        .style("mix-blend-mode", "multiply");

        

      if (edgeColor === "path") {
        const gradient = link
          .append("linearGradient")
          .attr("id", (d) => (d.uid = DOM.uid("link")).id)
          .attr("gradientUnits", "userSpaceOnUse")
          .attr("x1", (d) => d.source.x1)
          .attr("x2", (d) => d.target.x0);

        gradient
          .append("stop")
          .attr("offset", "0%")
          .attr("stop-color", (d) => color(d.source));

        gradient
          .append("stop")
          .attr("offset", "100%")
          .attr("stop-color", (d) => color(d.target));
      } //if
      

      link
        .append("path")
        .attr("class", "link")
        .attr("d", d3Sankey.sankeyLinkHorizontal)
        .style("stroke", (d) => findIndex(d.source.id))
        .attr("stroke-width", (d) => Math.max(1, d.width))
        .call(
          Lt
            ? (path) => path.append("title").text(({ index: i }) => Lt[i])
            : () => {}
        );

      link
        .append("title")
        .text((d) => `${d.source.id} → ${d.target.id}\n${format(d.value)}`);
          setTimeout(()=>{
            sankey.update({ nodes, links });
            link.selectAll(".link").attr("d", d3Sankey.sankeyLinkHorizontal());
          },0)

      function dragStart(event, d) {
        d.__x = event.x;
        d.__y = event.y;
        d.__x0 = d.x0;
        d.__y0 = d.y0;
        d.__x1 = d.x1;
        d.__y1 = d.y1;
      } //dragStart

      function dragMove(event, d) {
        d3.select(this).attr("transform", function (d) {
          const dx = event.x - d.__x;
          const dy = event.y - d.__y;
          d.x0 = d.__x0 + dx;
          d.x1 = d.__x1 + dx;
          d.y0 = d.__y0 + dy;
          d.y1 = d.__y1 + dy;

          if (d.x0 < 0) {
            d.x0 = 0;
            d.x1 = nodeWidth;
          } // if

          if (d.x1 > width) {
            d.x0 = width - nodeWidth;
            d.x1 = width;
          } // if

          if (d.y0 < 0) {
            d.y0 = 0;
            d.y1 = d.__y1 - d.__y0;
          } // if

          if (d.y1 > height) {
            d.y0 = height - (d.__y1 - d.__y0);
            d.y1 = height;
          } // if

          return `translate(${d.x0}, ${d.y0})`;
        }); //.attr('transform', function (d) {
        sankey.update({ nodes, links });
        link.selectAll(".link").attr("d", d3Sankey.sankeyLinkHorizontal());
      }
      return svg.node();
    }
    const timer = setTimeout(() => {
      SankeyChart(
        {
          node: [],
          links: [],
        },
        {
          nodeGroup: (d) => d.id.split(/\W/)[0], // take first word for color
          nodeAlign: "justify", // e.g., d3.sankeyJustify; set by input above
          linkColor: "source-target", // e.g., "source" or "target"; set by input above
          format: (
            (f) => (d) =>
              `${f(d)} TWh`
          )(d3.format(",.1~f")),
          width: 1000,
          height: 600,
        }
      );
    }, 0);

    return () => {
      clearTimeout(timer);
    };
  }, [object, count, findIndex]);

  async function SankeyChart(
    {
      nodes, // an iterable of node objects (typically [{id}, …]); implied by links if missing
      links, // an iterable of link objects (typically [{source, target}, …])
    },
    {
      format = ",", // a function or format specifier for values in titles
      align = "justify", // convenience shorthand for nodeAlign
      nodeId = (d) => d.id, // given d in nodes, returns a unique identifier (string)
      nodeImg = (d) => d.image,
      nodeGroup, // given d in nodes, returns an (ordinal) value for color
      nodeGroups, // an array of ordinal values representing the node groups
      nodeLabel, // given d in (computed) nodes, text to label the associated rect
      nodeTitle = (d) => `${d.id}\n${format(d.value)}`, // given d in (computed) nodes, hover text
      nodeAlign = align, // Sankey node alignment strategy: left, right, justify, center
      nodeWidth = 40, // width of node rects
      nodePadding = 15, // vertical separation between adjacent nodes
      linkSource = ({ source }) => source, // given d in links, returns a node identifier string
      linkTarget = ({ target }) => target, // given d in links, returns a node identifier string
      linkValue = ({ value }) => value, // given d in links, returns the quantitative value
      linkTitle = (d) => `${d.source} → ${d.target}\n${format(d.value)}`, // given d in (computed) links
      colors = d3.schemeTableau10, // array of colors
      width = 640, // outer width, in pixels
      height = 400, // outer height, in pixels
      marginTop = 5, // top margin, in pixels
      marginRight = 1, // right margin, in pixels
      marginBottom = 5, // bottom margin, in pixels
      marginLeft = 1, // left margin, in pixels
    } = {},
    scale ////// scale propert to scaling the path link
  ) {
    var DOM;

    console.log(object.links);

    document.getElementById("sankey").innerHTML = "";

    let colorData = [];

    await object.links.forEach((element) => {
      colorData.push(element.color);
    });

    colors = colorData;

    // Convert nodeAlign from a name to a function (since d3-sankey is not part of core d3).
    if (typeof nodeAlign !== "function")
      nodeAlign =
        {
          left: d3Sankey.sankeyLeft,
          right: d3Sankey.sankeyRight,
          center: d3Sankey.sankeyCenter,
        }[nodeAlign] ?? d3Sankey.sankeyJustify;
    // Compute values.
    const LS = d3.map(object.links, linkSource).map(intern);
    const LT = d3.map(object.links, linkTarget).map(intern);
    const LV = d3.map(object.links, linkValue);
    const Img = d3.map(object.nodes, nodeImg).map(intern);
    if (nodes === undefined)
      nodes = Array.from(d3.union(LS, LT), (id) => ({ id }));
    const N = d3.map(nodes, nodeId).map(intern);
    const G = nodeGroup == null ? null : d3.map(nodes, nodeGroup).map(intern);
    // Replace the input nodes and links with mutable objects for the simulation.
    nodes = d3.map(object.nodes, (el, i) => ({
      id: N[i],
      value: LV[i],
      image: Img[i],
    }));

    links = d3.map(object.links, (el, i) => ({
      source: LS[i],
      target: LT[i],
      value: LV[i],
    }));

    // if (!G && colors) linkColor = colors;
    // Compute default domains.
    if (G && nodeGroups === undefined) nodeGroups = G;

    // Construct the scales.
    const color =
      nodeGroup == null ? null : d3.scaleOrdinal(nodeGroups, colors);

    // Compute the Sankey layout.
    d3Sankey
      .sankey()
      .nodeId(({ index: i }) => N[i])
      .nodeAlign(nodeAlign)
      .nodeWidth(nodeWidth)
      .nodePadding(nodePadding)
      .extent([
        [marginLeft, marginTop],
        [width - marginRight, height - marginBottom],
      ])({ nodes, links });

    const sankey = d3Sankey
      .sankey()
      .nodeId((d) => d.name)
      .nodeAlign(10)
      .nodeWidth(15)
      .nodePadding(10)
      .extent([
        [1, 5],
        [900 - 1, 600 - 5],
      ]);

    // Compute titles and labels using layout nodes, so as to access aggregate values.
    if (typeof format !== "function") format = d3.format(format);
    const Tl =
      nodeLabel === undefined
        ? N
        : nodeLabel == null
        ? null
        : d3.map(nodes, nodeLabel);
    const Tt =
      nodeTitle == null ? null : d3.map(object.nodes, nodeTitle);
    const Lt =
      linkTitle == null ? null : d3.map(object.links, linkTitle);

    // A unique identifier for clip paths (to avoid conflicts).
    // const uid = `O-${Math.random().toString(16).slice(2)}`;

    const svg = d3
      .select("#sankey")
      .append("svg")
      .attr("width", width)
      .attr("height", height)
      .attr("viewBox", [0, 0, width, height])
      .attr("style", "max-width: 100%; height: auto; height: intrinsic;");
    // Container for draggable nodes
    const node = svg
      .selectAll("g")
      .data(nodes)
      .join("g")
      .attr("transform", (d) => `translate(${d.x0}, ${d.y0})`);

    // Relative to container
    node
      .append("rect")
      .attr("height", (d) => d.y1 - d.y0)
      .attr("width", (d) => d.x1 - d.x0)
      .attr("fill", color)
      .attr("stroke", "#000")
      .append("title")
      .text((d) => `${d.name}\n${format(d.value)}`);

    node
      .append("image")
      .attr("height", (d) => d.y1 - d.y0)
      .attr("width", (d) => d.x1 - d.x0)
      .attr("href", (d) => d.image);

    // Relative to container/ node rect
    node
      .append("text")
      .attr("font-family", "sans-serif")
      .attr("font-size", 10)
      .data(nodes)
      .attr("x", (d) => (d.x0 < width / 2 ? 6 + (d.x1 - d.x0) : -6)) // +/- 6 pixels relative to container
      .attr("y", (d) => (d.y1 - d.y0) / 2) // middle of node
      .attr("dy", "0.35em")
      .attr("text-anchor", (d) => (d.x0 < width / 2 ? "start" : "end"))
      .text(({ index: i }) => Tl[i]);

    if (G) node.attr("fill", ({ index: i }) => color(G[i]));
    if (Tt) node.append("title").text(({ index: i }) => Tt[i]);

    // d3V6 events: https://observablehq.com/@d3/d3v6-migration-guide#events
    node
      .attr("cursor", "move")
      .call(d3.drag().on("start", dragStart).on("drag", dragMove));

    var edgeColor;

    function intern(value) {
      return value !== null && typeof value === "object"
        ? value.valueOf()
        : value;
    }
    const link = svg
      .append("g")
      .attr("fill", "none")
      .attr("stroke-opacity", 0.5)
      .selectAll("g")
      .data(links)
      .join("g")
      .style("mix-blend-mode", "multiply");

    if (edgeColor === "path") {
      const gradient = link
        .append("linearGradient")
        .attr("id", (d) => (d.uid = DOM.uid("link")).id)
        .attr("gradientUnits", "userSpaceOnUse")
        .attr("x1", (d) => d.source.x1)
        .attr("x2", (d) => d.target.x0);

      gradient
        .append("stop")
        .attr("offset", "0%")
        .attr("stop-color", (d) => color(d.source));

      gradient
        .append("stop")
        .attr("offset", "100%")
        .attr("stop-color", (d) => color(d.target));
    } //if

    link
      .append("path")
      .attr("class", "link")
      .attr("d", d3Sankey.sankeyLinkHorizontal())
      .style("stroke", (d) => findIndex(d.source.id))
      .attr("stroke-width", (d) => Math.min(scale * d.width, d.width))
      .call(
        Lt
          ? (path) => path.append("title").text(({ index: i }) => Lt[i])
          : () => {}
      );

    link
      .append("title")
      .text((d) => `${d.source.id} → ${d.target.id}\n${format(d.value)}`);

    function dragStart(event, d) {
      d.__x = event.x;
      d.__y = event.y;
      d.__x0 = d.x0;
      d.__y0 = d.y0;
      d.__x1 = d.x1;
      d.__y1 = d.y1;
    } //dragStart

    function dragMove(event, d) {
      d3.select(this).attr("transform", function (d) {
        const dx = event.x - d.__x;
        const dy = event.y - d.__y;
        d.x0 = d.__x0 + dx;
        d.x1 = d.__x1 + dx;
        d.y0 = d.__y0 + dy;
        d.y1 = d.__y1 + dy;

        if (d.x0 < 0) {
          d.x0 = 0;
          d.x1 = nodeWidth;
        } // if

        if (d.x1 > width) {
          d.x0 = width - nodeWidth;
          d.x1 = width;
        } // if

        if (d.y0 < 0) {
          d.y0 = 0;
          d.y1 = d.__y1 - d.__y0;
        } // if

        if (d.y1 > height) {
          d.y0 = height - (d.__y1 - d.__y0);
          d.y1 = height;
        } // if

        return `translate(${d.x0}, ${d.y0})`;
      }); //.attr('transform', function (d) {
      sankey.update({ nodes, links });
      link.selectAll(".link").attr("d", d3Sankey.sankeyLinkHorizontal());
    }
    return svg.node();
  }

  //// increment Path Link

  const incrementPath = () => {
    setScale((prev) => prev * 1.5);
    SankeyChart(
      {
        node: [],
        links: [],
      },
      {
        nodeGroup: (d) => d.id.split(/\W/)[0], // take first word for color
        nodeAlign: "justify", // e.g., d3.sankeyJustify; set by input above
        linkColor: "source-target", // e.g., "source" or "target"; set by input above
        format: (
          (f) => (d) =>
            `${f(d)} TWh`
        )(d3.format(",.1~f")),
        width: 1000,
        height: 600,
      },
      scale
    );
  };

  /// decrement Path Link

  const decrementPath = () => {
    setScale((prev) => prev * 0.5);
    SankeyChart(
      {
        node: [],
        links: [],
      },
      {
        nodeGroup: (d) => d.id.split(/\W/)[0], // take first word for color
        nodeAlign: "justify", // e.g., d3.sankeyJustify; set by input above
        linkColor: "source-target", // e.g., "source" or "target"; set by input above
        format: (
          (f) => (d) =>
            `${f(d)} TWh`
        )(d3.format(",.1~f")),
        width: 1000,
        height: 600,
      },
      scale
    );
  };

  return (
    <>
      <section className="sankey-legend">
        <div className="legend">
          <ColorLegend />
        </div>
        <div className="sankey">
          <div className="countrytitle">
            <div>
              <img src={`${flagEndpoint}${apiDataValue.flagslug}`} alt="flag" />
            </div>
            <div>
              <h5>{apiDataValue.country}</h5>
              <span>({apiDataValue.years[apiDataValue.count]?.value})</span>
            </div>
            <div className="path-btns">
              <button onClick={incrementPath} ref={incremtRef}>
                +
              </button>
              <button onClick={decrementPath} ref={decrementRef}>
                -
              </button>
            </div>
          </div>
          <div id="sankey" ref={componentRef}>
            <PuffLoader
              color="#36D7B7"
              size={110}
              loader={!apiDataValue.loading}
              cssOverride={{
                display: "block",
                margin: "0 aut0",
                visibility: apiDataValue.loading ? "visible" : "hidden",
              }}
            />
          </div>
        </div>
      </section>
    </>
  );
};

export default D3sankey;
