import { Fragment } from "react";
import "./Country.css";
import { useDispatch } from "react-redux";
import {
  fetchData,
  changeCountry,
  changeFlag,
} from "../../redux/apiDataReducer";
import {
  flagEndpoint,
  getEmissionEndpoint as emissionEndpoint,
  getEnergyEndpoint as energyEndpoint,
} from "../../constant";

const Country = ({ countries }) => {
  const dispatch = useDispatch();

  return (
    <Fragment>
      <div className="country">
        {countries.map((country) => (
          <div style={{ cursor: "pointer" }} className="space-country">
            <div className="logo">
              <img src={`${flagEndpoint}${country.flagslug}`} alt="flag" />
            </div>
            <div className="logo-text">
              <span>{country.nomPays}</span>
            </div>
            <div className="emission-balance">
              <span
                onClick={() => {
                  dispatch(
                    fetchData({
                      cc: country.codePays,
                      endpoint: emissionEndpoint,
                    })
                  );
                  dispatch(changeCountry(country.nomPays));
                  dispatch(changeFlag(country.flagslug));
                }}
              >
                Emission Sankey
              </span>
              <span
                onClick={() => {
                  dispatch(
                    fetchData({
                      cc: country.codePays,
                      endpoint: energyEndpoint,
                    })
                  );
                  dispatch(changeCountry(country.nomPays));
                  dispatch(changeFlag(country.flagslug));
                }}
              >
                Balance Sankey
              </span>
            </div>
          </div>
        ))}
      </div>
    </Fragment>
  );
};

export default Country;
