import { Fragment } from "react";
import "../Sidebar/Sidebar.css";
import { AiOutlineRight } from "react-icons/ai";
import { useDispatch, useSelector } from "react-redux";
import { countryToggle, colorLegendToggle } from "../../redux/apiDataReducer";
import exportComponentAsSvg from "../../media/svg";
import { useComponentRef } from "../../Context/svgContext";
import exportComponentAsPNG from "../../media/png";

const Sidebar = () => {
  const componentRef = useComponentRef();

  const api = useSelector((state) => state.apiData);

  const dispatch = useDispatch();

  const colorPalete = () => {
    dispatch(colorLegendToggle());
  };

  const countryPicker = () => {
    dispatch(countryToggle());
  };

  const ElementShow = () => {
    let span = document.querySelectorAll("#span");
    for (var i = 0; i < span.length; i += 1) {
      span[i].style.display = "contents";
    }
  };

  const elementHide = () => {
    let span = document.querySelectorAll("#span");
    for (var i = 0; i < span.length; i += 1) {
      span[i].style.display = "none";
    }
  };

  // function myFunction(x) {
  //   if (x.matches) {
  //     let span = document.querySelectorAll("#span");
  //     for (let i = 0; i < span.length; i += 1) {
  //       span[i].style.display = "none";
  //     }
  //   } else {
  //     let span = document.querySelectorAll("#span");
  //   for (let i = 0; i < span.length; i += 1) {
  //     span[i].style.display = "contents";
  //   }
  //   }
  // }
  
  // var x = window.matchMedia("(max-width: 768px)")
  // myFunction(x);
  // x.addEventListener('resize',myFunction) 

  return (
    <Fragment>
      <nav onMouseOver={ElementShow} onMouseOut={elementHide}>
        <ul>
          <li onClick={countryPicker}>
            <img src="icons/Png/countries.png" alt="Countries" />
            <span id="span" style={{ display: "none" }}>
              Countries{" "}
              <AiOutlineRight
                style={{ marginLeft: "45px", marginTop: "5px" }}
              />
            </span>
          </li>
          <li onClick={colorPalete}>
            <img src="icons/Png/Color Legend.png" alt="Countries" />
            <span id="span" style={{ display: "none" }}>
              Color Legend{" "}
              <AiOutlineRight
                style={{ marginLeft: "20px", marginTop: "5px" }}
              />
            </span>
          </li>
          <li>
            <img src="icons/Png/Download.png" alt="Countries" />
            <span
              id="span"
              style={{ display: "none" }}
              onClick={() =>
                exportComponentAsSvg(
                  componentRef,
                  api.country,
                  api.years[api.count]?.value
                )
              }
            >
              Download SVG
            </span>
          </li>
          <li>
            <img src="icons/Png/Download.png" alt="Countries" />
            <span
              id="span"
              style={{ display: "none" }}
              onClick={() =>
                exportComponentAsPNG(
                  componentRef,
                  api.country,
                  api.years[api.count]?.value
                )
              }
            >
              Download PNG
            </span>
          </li>
          <li>
            <img src="icons/Png/Help.png" alt="Countries" />
            <span id="span" style={{ display: "none" }}>
              Help
            </span>
          </li>
        </ul>
      </nav>
    </Fragment>
  );
};

export default Sidebar;
