import { Slider } from "@mui/material";
import { Fragment, useEffect, useState } from "react";
import "../Timeline/Timeline.css";
import Box from "@mui/material/Box";
import { useDispatch, useSelector } from "react-redux";
import {
  // fetchData,
  incrementCount,
  updateCount,
} from "../../redux/apiDataReducer";
import { IntervalTimer } from "../../components/common/interval";
let interval;
const Timeline = () => {
  const [isShowbtn, setIsShowbtn] = useState(true);
  const [paused, setPaused] = useState(false);
  
  const dispatch = useDispatch();
  const apiDataValue = useSelector((state) => state.apiData.apiData);
  const years = useSelector((state) => state.apiData.years);
  const index = useSelector((state) => state.apiData.index);

  const [start, setStart] = useState(1000);
  const [value, setvalue] = useState("1x");


  useEffect(() => {
    interval = new IntervalTimer(() => {
      dispatch(incrementCount());
    }, start);
    if (start !== 1000) interval.clear();
  }, [dispatch, start]);

  const handleDataChange = (e, v, th) => {
    for (let i = 0; i < years.length; i++) {
      if (v === years[i].value) {
        dispatch(updateCount(years[i].value.toString()));
      }
    }
  };

  useEffect(() => {
     if (index === apiDataValue.length) {
      setIsShowbtn(true);
      setPaused(false);
      setIsShowbtn(true);
      interval.pause();
      interval.clear();
      dispatch(updateCount(years[0]?.value.toString()));
     }
    // 
  }, [index, years, apiDataValue.length,dispatch]);

  const stopAnimation = () => {
    setPaused(true);
    setIsShowbtn(true);
    interval.pause();
    interval.clear();
  };

  const changeSpeed=(x)=>{
    setvalue(x)
    if (x === "1x") {
      let time = 1000 - 500;
      setStart(time);
    } else if (x === "2x") {
      let time = 1000 - 800;
      setStart(time);
    }
  
  }
  return (
    <Fragment>
      <footer className="footer">
        <div className="slider">
          {isShowbtn ? (
            <img
              onClick={() => {
                setIsShowbtn(false);
                if (paused) interval.resume();
                else {
                  setPaused(false);
                  interval.start();
                }
              }}
              src="play.svg"
              alt="play"
              style={{
                marginRight: "20px",
                marginBottom: "20px",
                cursor: "pointer",
              }}
            />
          ) : (
            <img
              onClick={stopAnimation}
              src="pause.svg"
              alt="pause"
              style={{
                marginRight: "20px",
                marginBottom: "20px",
                cursor: "pointer",
              }}
            />
          )}
          <div className="div-btn">
            <select
              onChange={(event) => changeSpeed(event.target.value)}
              value={value}
            >
              <option value="1x" >
                1x
              </option>
              <option value="2x">
             2x
              </option>
            </select>
          </div>

          <Box sx={{ width: 800 }}>
            <Slider
              aria-label="Always visible"
              defaultValue={years[0]?.value}
              value={years?.value}
              min={years[0]?.value}
              max={years.at(-1)?.value}
              step={1}
              onChange={function (e, v, th) {
                handleDataChange(e, v, th);
                // alert(v);
              }}
              marks={years}
              valueLabelDisplay="off"
              color="success"
            />
          </Box>
        </div>
      </footer>
    </Fragment>
  );
};

export default Timeline;
