import { Fragment } from "react";
import { useDispatch, useSelector } from "react-redux";
import { changeColor } from "../../redux/apiDataReducer";
import "../ColorLegend/ColorLegend.css";

const ColorLegend = () => {
  const apiDataValue = useSelector((state) => state.apiData);

  const colorLegend = useSelector((state) => state.apiData.colorLegend);

  const dispatch = useDispatch();

  return (
    <Fragment>
      {colorLegend && (
        <div className="color-input">
          {Array.from(apiDataValue.ds).map((item) => (
            <div className="inputs">
              <input
                type={"color"}
                value={item.color}
                onChange={(e) => {
                  dispatch(
                    changeColor({
                      color: e.target.value,
                      source: item.source,
                    })
                  );
                }}
              />
              <span className="colorspan"> {item.source}</span>
            </div>
          ))}
        </div>
      )}
    </Fragment>
  );
};

export default ColorLegend;
