import { Fragment, useEffect, useRef, useState } from "react";
import Sidebar from "../components/Sidebar/Sidebar";
import D3sankey from "../components/D3/D3sankey";
import Header from "../components/Header/Header";
import Timeline from "../components/Timeline/Timeline";
import Country from "../components/Country/Country";
import { getEnergyEndpoint as energyEndpoint } from "../constant";
import { fetchData } from "../redux/apiDataReducer";
import { getCountriesEndpoint } from "../constant";
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";
import { useComponentRef } from "../Context/svgContext";
import { countryToggle } from "../redux/apiDataReducer";

const Home = () => {
  const [countries, setCountries] = useState([]);

  const countryPicker = useSelector((state) => state.apiData.countryPicker);

  const dispatch = useDispatch();

  const componentRef = useComponentRef();

  const country = useRef(null);

  useEffect(() => {
    const fetchCountries = async () => {
      const newCountries = await axios.get(`${getCountriesEndpoint}`);
      setCountries(newCountries.data);
    };
    fetchCountries();
  }, []);

  useEffect(() => {
    dispatch(
      fetchData({
        cc: "CEDEAO",
        endpoint: energyEndpoint,
      })
    );
  }, [dispatch]);

    const closeMenu = (event)=> {
      if (country.current && !country.current.contains(event.target)) {
        if(countryPicker === true){
          dispatch(countryToggle());
        }
      }
    }

    document.addEventListener("click", closeMenu);

  return (
    <Fragment ref={country}>
      <Header />
      <div className="display">
        <div className="sidebar">
          {countryPicker && <Country countries={countries} />}
          <Sidebar />
        </div>
        <div className="d3">
          <D3sankey ref={componentRef} />
        </div>
      </div>
      <Timeline />
    </Fragment>
  );
};

export default Home;
