const notifyDs = (newColor, source, state) => {
  let colorData = [...state.ds];

  for (let k = 0; k < colorData.length; k++) {
    if (colorData[k].source === source) {
      colorData[k].color = newColor;
    }
  }
};

const changeColorInData = (color, source, state) => {
  const UpdateColorData = [...state.object[state.count].links];
  UpdateColorData.forEach((outer) => {
    if (outer.source === source) {
      outer.color = color;
      notifyDs(color, source, state);
    }
  });
};

