export const fetchData = createAsyncThunk(`data/retrieve`, async (obj) => {
  return await axios
    .get(`${obj.endpoint}${obj.cc}`)
    .then((response) => response.data);
});

export const ApiDataSlice = createSlice({
  name: "apiData",
  initialState: initialState,
  reducers: {
    changeColor: (state, action) =>
      changeColorInData(action.payload.color, action.payload.source, state),
    incrementCount: (state) => {
      state.index += 1;
    },
    updateCount: (state, action) => {
      state.object = state.apiData[action.payload];
    },
    changeCountry: (state, action) => {
      state.country = action.payload;
      state.countryPicker = false;
    },
    changeFlag: (state, action) => {
      state.flagslug = action.payload;
    },
    colorLegendToggle: (state) => {
      state.colorLegend = !state.colorLegend;
      state.countryPicker = false;
    },
    countryToggle: (state) => {
      state.countryPicker = !state.countryPicker;
      state.colorLegend = false;
    },
  },

  extraReducers: (builder) => {
    builder
      .addCase(fetchData.pending, (state) => {
        state.loading = true;
      })
      .addCase(fetchData.fulfilled, (state, action) => {
        state.loading = false;
        state.apiData = action.payload;
        refineData(state);
        state.object = action.payload[state.index];
        state.apiData = action.payload;
      })
      .addCase(fetchData.rejected, (state, action) => {
        state.loading = false;
        state.apiData = [];
        state.object = {};
        state.error = action.error.message;
      });
  },
});

export const {
  changeColor,
  incrementCount,
  updateCount,
  changeCountry,
  changeFlag,
  colorLegendToggle,
  countryToggle,
} = ApiDataSlice.actions;

export default ApiDataSlice.reducer;
